@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,400;0,700;1,400;1,700&display=swap');

$font-stack: 'Titillium Web', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
             'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
             sans-serif;

$monospaced-font-stack: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
                        monospace;

body {
    margin: 0;
    font-family: $font-stack;
    font-weight: bold;  //override for contrast
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #323232;
    color: white;
}

a { color: #6cace4; }

code {
    font-family: $monospaced-font-stack;
}

%center {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

%sticky-top {
    position: sticky;
    top: 0;
}

// Homepage
#homepage {
    #logo {
        position: absolute;
        top: 0;
        z-index: 99999;
        min-height: 100vh;
        min-width: 100vw;
        background-color: #323232;
        
        #logo-frame {
            @extend %center;
            height: 100vh;
            z-index: 999999;

            img {
                max-height: 85%;
                max-width: 85%;
                vertical-align: middle;
            }
        }
    }

    // About Us section
    #about-us {
        background-color: #323232;
        padding: 2.75rem 0.5rem 0.5rem;  //account for navbar

        .contents {
            #featured-projects {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                flex-wrap: wrap;
                
                .project {
                    @extend %center;
                    position: relative;
                    overflow: hidden;
                    border: 1px solid white;
                    border-radius: 5px;
                    width: 49%;
                    margin-bottom: 1%;
                    max-height: 25vmin;
                    // max-width: 30vmin;

                    img {
                        background-repeat: no-repeat;
                        background-size: cover;
                        min-width: 100%;
                    }
                    
                    .caption-box {
                        @extend %center;
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        top: 0;
                        opacity: 0;
                        transition: opacity .1s ease-in;
                        background-color: rgba(255,255,255,0.5);
                        color: white;
                        text-shadow: 3px 3px 7px #323232;
                        text-align: center;
                        font-size: 2.5rem;
                        font-weight: 700;
                        overflow: hidden;
                    }
                    .caption-box:hover {
                        opacity: 1;
                    }
                }
            }
        }
    }

    // Team section
    #team {
        background-color: #323232;
        padding: 2.75rem 1.25rem 1.25rem;  //account for navbar

        .profiles {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            flex-wrap: wrap;

            .profile {          
                overflow: hidden;
                border: 1px solid white;
                border-radius: 5px;
                width: 100%;
                margin-bottom: 3%;
                padding: .75rem;
                display: flex;
                align-items: center;

                * {
                    padding: 0.1rem 0.5rem;
                    vertical-align: middle;
                }

                div:nth-child(3), div:last-of-type {
                    margin-bottom: 1rem;
                }
                
                .icon { padding: 0rem 0.5rem 0rem 0rem; }
                .icon-li { font-size: 35px; color: #0e76a8; }
                .icon-gh { font-size: 30px; color: #24292e; }
                
                img {
                    vertical-align: middle;
                    max-height: 25vmin;
                    max-width: 25vmin;
                }
                
                a { display: block; }
            }
        }

        @media (min-width: 1000px) {
            .profiles {
                justify-content: space-evenly;
                
                .profile {
                    width: 45%;
                    margin-bottom: 1%;
                }
            }
        }

        @media (min-width: 1500px) {
            .profiles {
                justify-content: space-evenly;
                
                .profile {
                    width: 30%;
                    margin-bottom: 1%;
                }
            }
        }
    }

    // Projects section
    #projects {
        background-color: #323232;
        padding: 2.75rem 1.25rem 1.25rem;  //account for navbar

        #project-list {
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            flex-wrap: wrap;

            .project {
                overflow: hidden;
                border: 1px solid white;
                border-radius: 5px;
                width: 100%;
                margin-bottom: 3%;
                padding: .75rem;
                display: flex;
                align-items: center;

                * {
                    padding: 0.25rem 0.5rem;
                    vertical-align: middle;

                    div, a {
                        padding: 0 0 1rem 0;
                    }
                }

                a b { padding: 0.25rem 0; }

                .team-roster a { padding: 0rem 0rem; }

                .team-roster a:after { content: ", "; }
                .team-roster a:last-child:after { content: ""; }
                
                img {
                    vertical-align: middle;
                    max-height: 25vmin;
                    max-width: 25vmin;
                }
            }

            @media (min-width: 1000px) {
                .project {
                    width: 75%;

                    .team-roster a {
                        padding: 0rem 0.75rem;
                    }
                    .team-roster a:after { content: ""; }
                    .team-roster a:last-child:after { content: ""; }
                }
            }
        }
    }
}



// Navbar
nav {
    @extend %sticky-top;
    display: flex !important;
    padding: 0.5rem;
    background-color: #222222;
    z-index: 9999;
    
    #nav-items {
        @extend %center;
        display: flex !important;
        flex-direction: row !important;
        * { margin-right: 0.75rem; }
        *:not(:first-of-type){ margin-right: 1.5rem; }
        img {
            height: 3.5rem;
            margin-left: 0.75rem;
        }
        a {
            color: white;
        }
    }
}

// Contents
.title {
    font-size: 3rem;
    font-weight: 700;
    margin: 1.5rem 0;
}

.subtitle {
    font-size: 1.5rem;
    font-weight: 700;
    margin: 0.5rem 0;
}

.contents {
    padding: 0.75rem;
    p {
        margin-left: 3rem;
        margin-right: 3rem;
        margin-bottom: 1.25rem;
    }
}

a {
    text-decoration: none;
    background-color: transparent;
    -webkit-text-decoration-skip: objects;
}
